import { makeFactory } from 'factory.ts';
import { Address, CustomerAccount, CustomerLocation } from './customer-account.interface';

export const addressTestFactory = makeFactory<Address>({
  id: '123',
});

export const customerLocationTestFactory = makeFactory<CustomerLocation>({
  id: '123',
  address: addressTestFactory.build(),
});

export const customerAccountTestFactory = makeFactory<CustomerAccount>({
  customerLocations: [customerLocationTestFactory.build()],
});
