import { createAction, props } from '@ngrx/store';
import { ViewState } from '../interfaces/view-state.interface';
import { CustomerProduct } from '../interfaces/customer-product.interface';
import { SdataSalesOffer } from '../interfaces/sdata-sales-offer.interface';
import { OmapiProduct } from '@telenet/ng-lib-omapi';
import { CustomerAccount } from '../interfaces/customer-account.interface';

export const setViewState = createAction('[internet-checkout] set viewstate', props<{ viewState: ViewState }>());

export const setCustomerProducts = createAction(
  '[internet-checkout] set customer products',
  props<{ customerProducts: CustomerProduct[] }>()
);

export const setCustomerAccount = createAction(
  '[internet-checkout] set customer account',
  props<{ customerAccount: CustomerAccount }>()
);

export const setSalesOffer = createAction(
  '[internet-checkout] request info - set sales offer',
  props<{ salesOffer: SdataSalesOffer[] }>()
);

export const setOmapiProducts = createAction(
  '[internet-checkout] request info - set omapi products',
  props<{ omapiProducts: OmapiProduct[] }>()
);
