import { CustomerAccount } from '../interfaces/customer-account.interface';

export class CustomerAccountUtil {
  static getAddressIdForLocation(customerAccount: CustomerAccount | undefined, locationId: string): string | undefined {
    // look for the customer location with the given locationId and return the address id
    const customerLocation = customerAccount?.customerLocations.find(
      (customerLocation) => customerLocation.id === locationId
    );
    return customerLocation?.address.id;
  }
}
