import { ViewState, ViewStateEnum } from '@sales/hardware-checkout/data-access';
import { SalesOrderState } from '@sales/goliath/data-access';
import { CustomerProduct } from '../interfaces/customer-product.interface';
import { SdataSalesOffer } from '../interfaces/sdata-sales-offer.interface';
import { OmapiProduct } from '@telenet/ng-lib-omapi';
import { CustomerAccount } from '../interfaces/customer-account.interface';

export interface CustomerData {
  customerProducts?: CustomerProduct[];
  customerAccount?: CustomerAccount;
}

export interface RequestInformation {
  salesOffer?: SdataSalesOffer[];
  omapiProducts?: OmapiProduct[];
}

export interface InternetCheckoutState {
  viewState: ViewState;
  salesOrderState: SalesOrderState;
  customerData: CustomerData | undefined;
  requestInfo: RequestInformation | undefined;
}

export const defaultState: InternetCheckoutState = {
  salesOrderState: SalesOrderState.INITIALIZED,
  viewState: {
    key: ViewStateEnum.STEPPER,
    showNavigation: true,
    showBackButton: true,
  },
  customerData: undefined,
  requestInfo: undefined,
};
