import { createFeatureSelector, createSelector } from '@ngrx/store';
import { InternetCheckoutState } from './internet-checkout.state';
import { stateFeatureName } from '../constants/state.const';

export const featureState = createFeatureSelector<InternetCheckoutState>(stateFeatureName);
export const viewState = createSelector([featureState], (state: InternetCheckoutState) => {
  return state.viewState;
});

export const salesOrderState = createSelector([featureState], (state: InternetCheckoutState) => {
  return state.salesOrderState;
});

export const customerDataState = createSelector([featureState], (state: InternetCheckoutState) => {
  return state.customerData;
});

export const salesOfferState = createSelector([featureState], (state: InternetCheckoutState) => {
  return state.requestInfo?.salesOffer;
});

export const salesOfferOmapiProductsState = createSelector([featureState], (state: InternetCheckoutState) => {
  return state.requestInfo?.omapiProducts;
});
