import { CustomerProduct } from '../interfaces/customer-product.interface';
import { CustomerProductStatusEnum } from '../enums/customer-product-status.enum';

const statusMap = [
  {
    status: CustomerProductStatusEnum.ACTIVE,
    values: [
      'Active',
      'Modification in progress',
      'Modification passed PONR',
      'Resuming in progress',
      'Resuming passed PONR',
      'Cancelling of suspension',
      'Cancelling of modification',
      'Cancelling of disconnection',
    ],
  },
  {
    status: CustomerProductStatusEnum.SUSPENDED,
    values: ['Suspended', 'Suspension in progress', 'Suspension passed PONR', 'Cancelling of resuming'],
  },
];

export class CustomerProductUtil {
  static isActiveOrSuspended = (customerProduct: CustomerProduct): boolean =>
    statusMap.some(
      ({ status, values }) =>
        values.includes(customerProduct.status) &&
        (status === CustomerProductStatusEnum.ACTIVE || status === CustomerProductStatusEnum.SUSPENDED)
    );

  static inUniqueLocation = (product: CustomerProduct, index: number, self: CustomerProduct[]): boolean =>
    index === self.findIndex((p) => p.locationId === product.locationId);
}
