import { Action, createFeature, createReducer, on } from '@ngrx/store';
import { CustomerData, defaultState, InternetCheckoutState, RequestInformation } from './internet-checkout.state';
import { stateFeatureName } from '../constants/state.const';
import { produce } from 'immer';
import {
  setCustomerAccount,
  setCustomerProducts,
  setOmapiProducts,
  setSalesOffer,
  setViewState,
} from './internet-checkout.actions';
import { ViewState } from '../interfaces/view-state.interface';
import { CustomerProduct } from '../interfaces/customer-product.interface';
import { SdataSalesOffer } from '../interfaces/sdata-sales-offer.interface';
import { OmapiProduct } from '@telenet/ng-lib-omapi';
import { CustomerAccount } from '../interfaces/customer-account.interface';

type PropertySetter<T> = <K extends keyof T>(
  draft: InternetCheckoutState,
  objectName: 'requestInfo' | 'customerData',
  property: K,
  value: T[K]
) => void;

const setProperty: PropertySetter<RequestInformation & CustomerData> = (draft, objectName, property, value) => {
  if (draft[objectName]) {
    draft[objectName] = { ...draft[objectName], [property]: value };
  } else {
    draft[objectName] = { [property]: value };
  }
};

export const reducer = createReducer(
  defaultState,
  on(setViewState, (state: InternetCheckoutState, props: { viewState: ViewState }) => {
    return produce(state, (draft: InternetCheckoutState) => {
      draft.viewState = props.viewState;
    });
  }),
  on(setCustomerProducts, (state: InternetCheckoutState, props: { customerProducts: CustomerProduct[] }) => {
    return produce(state, (draft: InternetCheckoutState) => {
      setProperty(draft, 'customerData', 'customerProducts', props.customerProducts);
    });
  }),
  on(setCustomerAccount, (state: InternetCheckoutState, props: { customerAccount: CustomerAccount }) => {
    return produce(state, (draft: InternetCheckoutState) => {
      setProperty(draft, 'customerData', 'customerAccount', props.customerAccount);
    });
  }),
  on(setSalesOffer, (state: InternetCheckoutState, props: { salesOffer: SdataSalesOffer[] }) => {
    return produce(state, (draft: InternetCheckoutState) => {
      setProperty(draft, 'requestInfo', 'salesOffer', props.salesOffer);
    });
  }),
  on(setOmapiProducts, (state: InternetCheckoutState, props: { omapiProducts: OmapiProduct[] }) => {
    return produce(state, (draft: InternetCheckoutState) => {
      setProperty(draft, 'requestInfo', 'omapiProducts', props.omapiProducts);
    });
  })
);

export function internetCheckoutReducer(
  state: InternetCheckoutState = defaultState,
  action: Action
): InternetCheckoutState {
  return reducer(state, action);
}

export const internetCheckoutFeature = createFeature({
  name: stateFeatureName,
  reducer: internetCheckoutReducer,
});
